/* Global Styles */
:root {
  /* --primary-color: #dc3545; */
  --dark-color: #333333;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  color: #333;
  background-color:#e3eafa;
}

a {
  color: var(--primary-color);
  text-decoration: none;
}

a:hover {
  color: #666;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}
.temprary:hover{
  color:red;
  
}

/* Utilities */
.container {
  max-width: 1100px;
/* width: 50%; */
  margin: auto;
  /* margin-bottom: 15px; */
  /* overflow: auto;
  height: 80vh; */
  padding: 0 2rem;
  
}

/* Text Styles*/
.x-large {
  font-size: 4rem;
