}

/* Center All */
.all-center {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: auto;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* Cards */
.card {
  padding: 1rem;
  border: #ccc 1px;
  margin: 0.7rem 0;
  box-shadow: 0 4px 8px 0 rgb(0,0,0,0.2);
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background-color: white;
  /* border-radius: 10px; */
}
